@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  min-width: 320px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011C40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  background-color: #f9fbfd;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.background-image {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  transform: translate(-50%, -50%);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 50px;
  z-index: 1;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.form__field input {
  width: 100%;
  padding: 13px 16px 12px;
  font-size: 14px;
  line-height: 14px;
  color: #011C40;
  outline: none;
  border: none;
  background-color: #f9fbfd;
  box-shadow: none;
}
.form__field input::placeholder {
  color: rgba(1, 28, 64, 0.7);
}
.form__field input:hover, .form__field input:focus-visible {
  box-shadow: inset 0 0 0 2px #2c39f2;
}

.form__checkbox label {
  display: flex;
  align-items: center;
  font-size: 10px;
  line-height: 13px;
  user-select: none;
  cursor: pointer;
}

.form__checkbox .form__custom-checkbox {
  position: relative;
  min-width: 18px;
  min-height: 18px;
  width: 18px;
  height: 18px;
  margin-right: 5px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__checkbox .form__custom-checkbox {
    min-width: 20px;
    min-height: 20px;
    width: 20px;
    height: 20px;
  }
}

.form__checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
@media (hover: hover) {
  .form__checkbox input:hover:not(.focus-visible) ~ .form__custom-checkbox {
    border: 2px solid #2c39f2;
  }
}
.form__checkbox input.focus-visible:focus ~ .form__custom-checkbox {
  border: 2px solid #2c39f2;
}

.form__checkbox input:checked ~ .form__custom-checkbox::after {
  content: "";
  position: absolute;
  top: calc(50% - 1px);
  left: 50%;
  width: 7px;
  height: 10px;
  border-bottom: 2px solid #011C40;
  border-right: 2px solid #011C40;
  transform: translate(-50%, -50%) rotate(45deg);
}

.connection {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .connection {
    align-items: flex-start;
    flex-direction: column;
  }
}

.connection a {
  display: block;
  padding: 12px 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}
@media (max-width: 1023px) {
  .connection a {
    padding: 5px 12px;
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .connection a {
    padding: 12px 10px;
    margin-left: -10px;
    font-size: 12px;
    line-height: 15px;
  }
}

.main-header {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  transform: translateX(-50%);
  padding: 35px 0;
  color: #e5e5e5;
  z-index: 10;
}
@media (max-width: 1023px) {
  .main-header {
    padding: 30px 0;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding: 20px 0;
  }
}

.main-header__wrapper {
  display: flex;
  gap: 10px;
}
@media (max-width: 767px) {
  .main-header__wrapper {
    position: static;
  }
}

.main-header__logo {
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .main-header__logo {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .main-header__logo {
    width: 42px;
    height: 22px;
  }
}

.main-header__connection {
  margin: 0 -12px 0 auto;
}
@media (max-width: 1023px) {
  .main-header__connection {
    margin: 0 -11px 0 auto;
  }
}
@media (max-width: 767px) {
  .main-header__connection {
    display: none;
    margin: 0;
  }
}

.main-header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-header__toggle {
    position: absolute;
    top: 11px;
    right: 8px;
    z-index: 2;
    display: block;
    width: 40px;
    height: 40px;
    cursor: pointer;
    border: none;
    background-color: rgba(255, 255, 255, 0);
    box-sizing: border-box;
  }
  .main-header__toggle::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 -6px 0 0 #f9fbfd;
    transform: translate(-50%, -50%);
  }
}

@media (max-width: 767px) {
  .is-open.main-header {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background-color: rgba(0, 43, 102, 0.5);
    transform: none;
    color: #011C40;
  }
  .is-open .main-header__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    width: 284px;
    height: 100%;
    padding: 20px 15px;
    flex-direction: column;
    gap: 22px;
    background-color: #f9fbfd;
    z-index: 101;
  }
  .is-open .main-header__logo {
    fill: #011C40;
  }
  .is-open .main-header__connection {
    display: block;
  }
  .is-open .connection a {
    padding: 14px 20px;
    font-size: 18px;
    line-height: 22px;
  }
  .is-open .main-header__toggle {
    right: 2px;
  }
  .is-open .main-header__toggle::before, .is-open .main-header__toggle::after {
    content: "";
    position: absolute;
    top: 19px;
    left: 12px;
    width: 18px;
    height: 2px;
    background-color: #011C40;
  }
  .is-open .main-header__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .is-open .main-header__toggle::after {
    transform: rotate(-45deg);
  }
}
.reservations {
