  }
}
@media (max-width: 767px) {
  .reservations__text-wrapper p {
    max-width: 260px;
    font-size: 16px;
    line-height: 22px;
  }
}

.reservations__form-wrapper {
  width: 403px;
  padding: 43px 50px 50px;
  color: #f9fbfd;
  background-color: #011C40;
}
@media (max-width: 767px) {
  .reservations__form-wrapper {
    width: 100%;
    padding: 32px 30px 40px;
  }
}

.reservations__form-wrapper p {
  margin: 0 0 26px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 1023px) {
  .reservations__form-wrapper p {
    font-size: 18px;
    line-height: 27px;
  }
}
@media (max-width: 767px) {
  .reservations__form-wrapper p {
    margin: 0 0 24px;
    text-align: center;
  }
}

.reservations__form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.reservations__form-button {
  margin: 11px 0 0;
}
@media (max-width: 767px) {
  .reservations__form-button {
    margin: 4px 0 0;
  }
}

.card {
  position: relative;
  display: block;
  min-height: 386px;
  outline: none;
}

.card h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}

.card__back,
.card__front {
  width: 100%;
  min-height: 100%;
  display: block;
  overflow: hidden;
  backface-visibility: hidden;
  transition: transform 300ms linear;
  color: #f9fbfd;
  background-color: #011C40;
}

.card__front {
  position: absolute;
  display: flex;
  padding: 30px 30px 22px;
  transform: perspective(300px) rotateY(0deg);
}
@media (max-width: 1023px) {
  .card__front {
    padding: 20px 35px 27px;
  }
}
@media (max-width: 767px) {
  .card__front {
    padding: 25px 25px 15px;
  }
}

.card__front h3 {
  position: relative;
  margin: auto 0 0;
  z-index: 1;
}

.card__back {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 28px 25px 30px;
  transform: perspective(300px) rotateY(180deg);
}
@media (max-width: 1023px) {
  .card__back {
    padding: 28px 35px 35px;
  }
}
@media (max-width: 767px) {
  .card__back {
    padding: 25px 25px 35px;
  }
}

.card__back h3 {
  margin: 0 0 15px;
}

.card__text-wrapper {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin: 0 0 6px;
}

.card__text-block h4 {
  margin: 0 0 2px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}

.card__text-block p {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
}

.card__button {
  margin: auto 0 0;
}

.card:focus-visible > .card__front,
.card:hover > .card__front {
  transform: perspective(300px) rotateY(-180deg);
}

.card:focus-visible > .card__back,
.card:hover > .card__back {
  transform: perspective(300px) rotateY(0deg);
}

.catalog {
  padding: 85px 0 120px;
}
@media (max-width: 1023px) {
  .catalog {
    padding: 90px 0 100px;
  }
}
@media (max-width: 767px) {
  .catalog {
    padding: 60px 0 65px;
  }
}

.catalog h2 {
  display: block;
  margin: 0 0 50px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  .catalog h2 {
    margin: 0 0 48px;
    font-size: 26px;
    line-height: 39px;
  }
